.hso-gradient-btn {
  width: fit-content;
  height: auto;
}

.hso-gradient-btn .wp-element-button, .hso-gradient-btn .wp-block-navigation-item__content {
    padding: 5px 20px !important;
    width: auto !important;
    height: auto !important;
    border: none;
    outline: none;
    position: relative;
    z-index: 1;
    border-radius: 100px !important;
    background: $hso-button-gradient;
    color: black;
    cursor: pointer;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 600;

    //Overrides from navigation links
    .wp-block-navigation-item__label {
      padding: 0 !important;
      width: auto !important;
      border: none !important;
      border-radius: 0 !important;
    }

    &:hover {
      //Overrides from navigation links
      .wp-block-navigation-item__label {
        border: none !important;
        border-radius: 0 !important;
        background-color: $hso-button-gradient !important;
        color: white !important;
      }
    }
  }
  .hso-gradient-btn .wp-element-button::before, .hso-gradient-btn .wp-block-navigation-item__content::before {
    content: "";
    position: absolute;
    left: 1px;
    right: 1px;
    top: 1px;
    bottom: 1px;
    border-radius: 100px;
    background-color: white;
    z-index: -1;
    transition: 200ms;
    width: auto !important;
    height: auto !important;
    padding: 5px 20px !important;
  }
  .hso-gradient-btn .wp-element-button::after, .hso-gradient-btn .wp-block-navigation-item__content::after {
    content: attr(data);
    color: #000;
    transition: 200ms;
  }
  
  .hso-gradient-btn .wp-element-button:hover::before, .hso-gradient-btn .wp-block-navigation-item__content:hover::before {
    opacity: 0;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
  }
  .hso-gradient-btn .wp-element-button:hover, .hso-gradient-btn .wp-block-navigation-item__content:hover {
    color: white;
  }


  //Dark BG vs Light BG
  .hso-gradient-btn-dark .wp-element-button::before {
    background-color: black;
  }

  .hso-gradient-btn-dark .wp-element-button {
    color: white;
  }

  //Grey 200 background
  .hso-gradient-btn-grey-200 .wp-element-button::before {
    background-color: $gray-200;
  }